/* src/components/Header.css */

/* ── 공통 컨테이너 (1200px 중앙) */
/* ── 공통 컨테이너 (1200px 중앙) */
/* 공통 */

* {margin: 0;padding: 0;}

/* ==============================================================
   Kurly Header  (클래스 접두어 kh‑)
   스크린샷 #2 와 동일한 간격·폭(1050px)·정렬로 맞춘 완전 버전
   ============================================================== */

/* ─────────── 공통 컨테이너 ─────────── */
.kh-header{  /* ★ 추가 : 아래 방향만 살짝 드롭‑쉐도우 */
  box-shadow:0 3px 6px -2px rgba(0,0,0,.08);}
  .kh-inner1{width: 1200px; margin: 0 auto;}
.kh-wrap          {width:1050px;margin:0 auto;box-sizing:border-box}
a                 {text-decoration:none;color:inherit}

/* ───────── 0) 상단 배너 ───────── */
.kh-banner        {background:#5f0080;color:#fff;height:36px;line-height:36px;
                   text-align:center;font-size:14px;position:relative;z-index:500}
.kh-banner-close  {position:absolute;top:0;right:20px;width:36px;height:36px;
                   border:none;background:none;color:#fff;font-size:18px;cursor:pointer}

/* ───────── 1) 회원 메뉴 ───────── */
.kh-member        {font-size:13px;padding:6px 0;display:flex;justify-content:flex-end}
.kh-member ul     {display:flex;gap:16px;list-style:none;padding:0;margin:0}

/* ───────── 2) TOP (로고 | 검색 | 아이콘) ───────── */
.kh-top           {  display:flex;                     /* grid → flex  */
  justify-content:space-between;    /* 좌·우 균등 배치 */
  align-items:center;
  height:80px;}

/* 2‑1) 로고 & 브랜드 */
.kh-logo-area     {display:flex;align-items:center}
.kh-logo img      {display:block}
.kh-brand         {display:flex;align-items:center;gap:12px;margin-left:20px;
                   font-size:15px;font-weight:600}
.kh-brand a       {color:#5f0080}
.kh-brand span    {color:#d1d1d1;font-weight:400}

/* 2‑2) 검색창 */
.kh-search        {display:flex;justify-content:center}
.kh-search input  {width:520px;height:40px;border:2px solid #5f0080;border-right:none;
                   border-radius:4px 0 0 4px;padding:0 12px;font-size:14px}
.kh-search button {width:40px;height:44px;border:2px solid #5f0080;border-left:none;
                   border-radius:0 4px 4px 0;background:#fff;color:#5f0080;
                   font-size:18px;cursor:pointer}

/* 2‑3) 상단 아이콘 */
.kh-icon-box      {display:flex;gap:26px;font-size:22px;color:#333}

/* ───────── 3) STICKY 네비게이션 ───────── */
.kh-nav-sticky   { margin: 0 auto; position:sticky;
  top:0;
  background:#fff;
  z-index:400;}
.kh-nav           {  display:flex;
  align-items:center;
  justify-content:space-between;   /* ← 스페이스‑비트윈 */
  height:56px;}

/* 3‑1) 카테고리 + 메가메뉴 */
.kh-category  {display:flex;align-items:center;gap:4px;font-size:15px;cursor:pointer; gap:4px;font-size:15px;cursor:pointer}
.kh-category .fa-bars{font-size:18px}

/* 메가메뉴 컨테이너 (hover 시 표시) */
.kh-mega          {position:absolute;top:100%;left:0;display:none;width:500px;
                   background:#fff;border:1px solid #ddd;
                   box-shadow:0 2px 6px rgba(0,0,0,.08)}
.kh-category:hover .kh-mega{display:flex}

/* 1‑depth 영역 */
.kh-mega-left     {width:220px;border-right:1px solid #eee;padding:16px 0;
                   display:flex;flex-direction:column}
.kh-mega-left a   {display:flex;align-items:center;gap:8px;padding:8px 16px;
                   font-size:14px;color:#333;transition:.1s}
.kh-mega-left a.on,
.kh-mega-left a:hover{background:#faf5ff;color:#5f0080;font-weight:600}

/* 2‑depth 영역 (슬라이드 등장) */
.kh-mega-right       {flex:1;padding:16px 20px;
                       opacity:0;transform:translateX(-10px);
                       transition:opacity .18s ease-out,transform .18s ease-out}
.kh-mega-right a     {display:block;margin-bottom:10px;font-size:14px;color:#333}
.kh-mega-right a:hover{color:#5f0080}
.kh-mega-left a.on ~ .kh-mega-right,
.kh-mega-right.show  {opacity:1;transform:translateX(0)}  /* JS에서 .show 추가 */

/* 3‑2) 메인 링크 (신상품 ~ 특가/혜택) */
.kh-main-links    {display:flex;
  justify-content:center;          /* 가운데에 모음 */
  gap:120px;
  flex:1;                          /* 남은 공간 채우기 */
  font-size:15px;}
.kh-main-links a  {padding-bottom:4px;color:#333;border: 2px solid #fff; transition:.15s}
.kh-main-links a:hover{color:#5f0080;border-bottom:2px solid #5f0080}

/* 3‑3) 배송안내 버튼 */
.kh-ship          {padding:6px 18px;font-size:13px;color:#5f0080;
  background:#fff;border:1px solid #5f0080;border-radius:20px;cursor:pointer}



