/* Kullylog.js 로그인페이지*/
.logfull{padding: 74px 0 246px 0;}

.klogform{
  width: 380px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.klogform input[type="text"]{
  width: 100%;
  height: 46px;
  padding: 0 12px;
  margin-bottom: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  box-sizing: border-box;
}

.kloglabel{
  display: none;
}

.findetxtbox {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  gap: 1px;
  font-size: 14px;
  color: #333;
}

.findetxtbox a{
  font-size: 14px;
  color: #333;
  text-decoration: none;
}

/* 아이디 찾기 뒤에 세로선 넣기 */
.findetxtbox a:first-child::after {
  content: '|';
  margin: 0 4px;
  color: #333; /* 회색 세로선 */
}

/* 비밀번호 찾기에는 세로선 안 넣음 */
/* .findetxtbox a:last-child::after {
  content: '';
} */

/* 1) .klogbutton 자체 레이아웃 */
.klogform .klogbutton {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

/* 2) 모든 버튼 공통 */
.klogform .klogbutton input[type="button"] {
  width: 100%;
  height: 48px;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  box-sizing: border-box;
}

/* 3) 로그인 버튼 (name="name") */
.klogform .klogbutton input[name="name"] {
  background-color: #5f0080;
  color: #fff;
  border: none;
}

/* 4) 회원가입 버튼 (name="pw_name") */
.klogform .klogbutton input[name="pw_name"] {
  background-color: #fff;
  color: #5f0080;
  border: 1px solid #5f0080;
}



